.window {
  margin: 60px 150px;
  border-radius: 15px;
  background: #fff;
  overflow: hidden;
  height: calc(100vh - 236px);
  .top {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px;
    height: 30px;
    background: #e8eaed;
    .xBt {
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      font-size: 12px;
      content: "";
      background: rgb(237, 106, 94);
      height: 15px;
      width: 15px;
      border-radius: 100%;
      top: 50%;
      transform: translateY(-50%);
      left: 10px;
      color: transparent;
      cursor: pointer;
      &:hover {
        color: black;
      }
    }
    .yBt {
      content: "";
      position: absolute;
      height: 15px;
      width: 15px;
      border-radius: 100%;
      top: 50%;
      transform: translateY(-50%);
      background: rgb(255, 177, 60);
      left: 35px;
    }
    .gBt {
      content: "";
      position: absolute;
      height: 15px;
      width: 15px;
      border-radius: 100%;
      top: 50%;
      transform: translateY(-50%);
      background: rgb(29, 192, 70);
      left: 60px;
    }
    div {
      display: flex;
      align-items: center;
      justify-content: center;
      border: 2px solid #bfbfc1;
      border-radius: 5px;
      width: 600px;
      height: 25px;
      color: rgb(79, 79, 79);
    }
  }
  .content {
    background: #fff;
    padding: 60px;
    height: calc(100% - 160px);
    overflow-y: scroll;
    &::-webkit-scrollbar {
      width: 10px;
    }
    &::-webkit-scrollbar-track {
      background: transparent;
    }
    &::-webkit-scrollbar-thumb {
      border-radius: 10px;
      background: #bcbcbc;
    }
  }
}

@media (max-width: 1700px) {
  .window {
    margin: 60px 80px;
  }
}
@media (max-width: 1550px) {
  .window {
    margin: 60px 60px;
  }
}
@media (max-width: 1280px) {
  .window {
    margin: 30px 30px;
    height: calc(100vh - 176px);
  }
}

@media (max-width: 884px) {
  .window {
    .top {
      div {
        width: 400px;
      }
    }
    .content {
      padding: 30px;
      height: calc(100% - 100px);
    }
  }
}

@media (max-width: 428px) {
  .window {
    margin: 10px 10px;
    height: calc(100vh - 136px);
    .top {
      .xBt {
        height: 10px;
        width: 10px;
      }
      .yBt {
        height: 10px;
        width: 10px;
        left: 25px;
      }
      .gBt {
        height: 10px;
        width: 10px;
        left: 40px;
      }
      div {
        width: 200px;
        font-size: 14px !important;
      }
    }
    .content {
      padding: 20px;
      height: calc(100% - 80px);
    }
  }
}
