.docks {
  justify-content: center;
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  .dock {
    position: relative;
    .tag {
      position: absolute;
      top: -30px;
      left: 50%;
      transform: translateX(-50%);
      width: 90px;
      height: 25px;
      line-height: 25px;
      border-radius: 5px;
      text-align: center;
      font-size: 15px;
      color: white;
      background-color: rgba($color: #000000, $alpha: 0.5);
      display: none;
    }
    .hover {
      z-index: 99;
      display: block;
    }
  }
}
